import { Component, Input, Output, OnInit, DoCheck, ViewChild, EventEmitter } from '@angular/core';
import { PermissionService, MenuService, FilterByGroup, CookiesService, CenterService} from '../shared/services';
import { Observable } from 'rxjs/Observable';

import { overlayInterface } from './overlayConfiguration.interface'
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/observable/throw';

@Component({
  selector: 'delhivery-services-menu-overlay',
  template: `
    <div class="services-overlay" [ngClass]="{'open': open}" *ngIf="serviceMenu">
        <ul *ngIf="navLink.length">
            <li class="nav-list" *ngFor="let nav of navLink">
                <a href="{{nav.url}}" target="{{(nav.tlLink) ? '_blank' : '_self'}}" [ngClass]="{'performance': nav.tlLink}">
                    <span class="left-arrow" *ngIf="!nav.tlLink">&larr;</span>&nbsp;
                    <span>{{nav.title}}</span>
                </a>	
            </li>
        </ul>
        <div class="service-overlay-content" *ngIf="!serviceMenu.display">
            <p>No Services Menu</p>
        </div>
        <div *ngIf="serviceMenu.display" class="service-overlay-content">

            <div class="service-container">
                <div class="frequent-services" *ngIf="recentUsedService.length">
                    <h3 class="frequent-header">Recently Used</h3>
                    <div class="recent-activity">
                        <a [href]="menu.page_url" target="_blank" class="frequent-list-items" *ngFor="let menu of recentUsedService">
                            <span class="sub-menu-item">
                                {{menu.alias || menu.title}}
                            </span>
                        </a>
                    </div>
                </div>
                <div class="menu-list">
                    <div class="search-services">
                        <div class="form-search">
                            <input type="text" [(ngModel)]="searchService" placeholder="Search Services" class="search-input" />
                            <span class="fa fa-search"></span>
                        </div>
                    </div>
                    <div class="menu-services" DynamicHeight>
                        <div class="menu-service-group" *ngFor="let obj of serviceMenu.service, let i=index">
                            <p *ngIf="(obj.submenu | filterByGroup : {'title' : searchService} : false).length" class="service-title semi-bold" [hidden]="!obj.display">{{obj.grouptitle}}</p>
                            <a [href]="menu.page_url" target="_blank" *ngFor="let menu of (obj.submenu | filterByGroup : {'title' : searchService} : false)" [hidden]="!menu.display" (click)="recentUsed(menu)">
                                <span class="sub-menu-item">
                                    {{menu.title}}
                                </span>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
  `,
  styles: [`
    .services-overlay{height:0;width:100%;position:fixed;z-index:1;left:0;top:0;overflow-y:hidden}.services-overlay.open{height:calc(100% - 50px);width:98%;top:40px;overflow-y:auto;background:white;border:1px solid #c3bdbd;margin:0 1%;border-top:none}.services-overlay ul{margin:0;padding:0}.services-overlay ul .nav-list{list-style:none;float:left;margin-left:30px}.services-overlay ul .nav-list a{font-family:OpenSans-SemiBold;text-decoration:underline;color:#F0503C;font-size:13px}.services-overlay ul .nav-list a:hover{opacity:1}.services-overlay ul .nav-list a.performance{text-decoration:none !important}.services-overlay ul .nav-list a .left-arrow{font-size:19px;line-height:12px;font-weight:bold}.services-overlay ul .nav-list a span{text-decoration:underline}.services-overlay .loading-text{font-size:20px}.services-overlay .service-overlay-content{position:relative;margin:30px}.services-overlay .service-overlay-content p{color:#666;font-family:Opensans-Semibold;font-size:14px}.services-overlay .service-overlay-content .service-container{display:flex}.services-overlay .service-overlay-content .service-container .frequent-services{flex:1 1 15%;border-right:2px solid #afaaaa;padding-right:15px}.services-overlay .service-overlay-content .service-container .frequent-services .frequent-list-items{text-align:left}.services-overlay .service-overlay-content .service-container .frequent-services .frequent-header{text-align:left}.services-overlay .service-overlay-content .service-container .menu-list{flex:1 1 85%}.services-overlay .service-overlay-content .service-container .menu-list .search-services{margin:20px 40px}.services-overlay .service-overlay-content .service-container .menu-list .search-services .form-search{position:relative}.services-overlay .service-overlay-content .service-container .menu-list .search-services .form-search span{position:absolute;right:8px;top:8px;color:#808080;font-size:14px}.services-overlay .service-overlay-content .service-container .menu-list .search-services .search-input{width:calc(100% - 10px);padding:3px 5px;border:none;box-shadow:none !important;border-radius:0;border-bottom:1px solid #a6a6a6;font-size:14px;line-height:21px;color:#414042;margin-bottom:10px;background:transparent}.services-overlay .service-overlay-content .service-container .menu-list .search-services .search-input:focus{border-color:red;outline:none;box-shadow:none}.services-overlay .service-overlay-content .service-container .menu-list .menu-services{display:flex;flex-flow:column wrap;min-height:450px;width:calc(100% - 80px);align-content:flex-start;padding-left:40px}.services-overlay .service-overlay-content .service-container .menu-list .menu-services .menu-service-group{width:20%;text-align:left;margin-right:40px}.services-overlay .service-overlay-content .service-container .menu-list .menu-services .menu-service-group .service-title{text-transform:capitalize;font-weight:300;color:#F0503C;font-size:16px}.services-overlay .service-overlay-content .service-container .menu-list .menu-services .menu-service-group .no-result{position:absolute;width:calc(100% - 275px);text-align:center}.services-overlay a{display:inherit;margin:5px}.services-overlay a .sub-menu-item{font-size:14px;color:#323232}.services-overlay a .sub-menu-item:hover{color:#e47911}.no-services{color:black}.service-menu-wrap{width:auto;display:block}[hidden]{display:none !important}@media screen and (max-width: 640px){.services-overlay.open{top:100px}.services-overlay .service-overlay-content{margin:0;padding:20px 10px}.services-overlay .service-overlay-content .service-container{display:block;padding:0}.services-overlay .service-overlay-content .service-container .frequent-services{border-right:none;padding-right:15px;text-align:left;padding-bottom:10px;margin:0 0 10px 0;border-bottom:1px dashed #ccc}.services-overlay .service-overlay-content .service-container .menu-list{padding:0;margin:0}.services-overlay .service-overlay-content .service-container .menu-list .search-services{margin:20px 0}.services-overlay .service-overlay-content .service-container .menu-list .menu-services{flex-flow:row wrap;margin:20px 0;padding:0}.services-overlay .service-overlay-content .service-container .menu-list .menu-services .menu-service-group{width:100%;margin-right:0}}
  `]
})
export class ServicesMenuOverlayComponent implements OnInit, DoCheck {
  @Input () profile : object;
  @Input () open : boolean;
  @Output () onError : EventEmitter<any> = new EventEmitter<any>();

  

  private umsPermission : Array<any> = [];
  private HQPermission : Array<any> = [];
  private allPermission : Array<any>;
  private isUms : boolean = false;
  private isHq : boolean = false;
  public serviceMenu : overlayInterface;
  private filter = new FilterByGroup();
  private errMsg : string;
  private urlLink : string;
  public homeUrlLink : string;
  private superUser : boolean = false;
  private navLink : Array<any> = [];
  
  private getRecent;
  private recentUsedService;
  private env : string;

  constructor(private menuService : MenuService, private centerService: CenterService, private cookies : CookiesService, private permission : PermissionService) {
    this.env = this.menuService.getOptions()['environment'];
    this.getRecent = this.cookies.get(`${this.env}_service_name`) ? this.cookies.get(`${this.env}_service_name`).split(',') : [];

    this.urlLink = this.menuService.getUrlLink()[this.env];
    this.homeUrlLink = `${this.urlLink}p/center/`;
  }

  ngOnInit() {
    this.getServicesMenu();    
  }


  ngDoCheck(){

    if(this.isUms && this.isHq){
      
      this.allPermission = this.permission.mergePermission(this.umsPermission,this.permission.getHqPermArray());
      this.serviceMenu = this.permission.updatedPermission(this.allPermission);
      this.recentUsedService = this.permission.recentServiceFilter(this.serviceMenu, this.getRecent);      
    
    } else if(this.superUser && this.isUms){

      this.serviceMenu = this.permission.updatedPermission();
      this.recentUsedService = this.permission.recentServiceFilter(this.serviceMenu, this.getRecent); 
    }
  }

  /**
   * @name getServicesMenu
   * @description This method is using to set menu service as per user's permissions which are based on ums and hq
   */
  private getServicesMenu(){
    this.permission.getServicesMenu()
      .subscribe(res => {
        this.permission.setServiceName(res['data']);
        
        //Call Function for HQ permissions
      
        this.getHQ();
        
        //Call Function for UMS permissions
     
        this.getUms();

      }, error =>{
        this.onError.emit('Not able to Build Services Menu.');
      })
      return this.onError; 
  }
  

  /**
   * @name getUms
   * @description This method is using to get all permission assigned to logged in user.
   */
  private getUms(){
    this.permission.getUMSPermissions(this.profile['uuid'])
      .subscribe(res => {
            if(res && res['permissions']){
                let perm = res['permissions'];
                for(let i=0 ; i < perm.length ; i++){
                    this.umsPermission.push(perm[i].name)
                }
                this.isUms = true;
                this.navLink = this.permission.hasPerformancePerm(this.urlLink,this.umsPermission);
            }
        }, error => {
          this.isUms = true;
          this.navLink = this.permission.hasPerformancePerm(this.urlLink,this.umsPermission);
      
          // this.onError.emit('Not able to fetch Permission from UMS.');
        })
        return this.onError; 
  }


  /**
   * @name getHQ
   * @description This method is using to get all permission assigned to logged in user in hq.
   */
  private getHQ(){
    this.permission.getHQPermission()
      .subscribe(res => {
          if(res && res['superuser']){
            this.superUser = true;
           
          } else if(res && res['perms']){
            
            this.permission.createPermissionHq(res);  
            this.isHq = true;       
          
          }
        }, error =>{
          
          this.onError.emit('Not able to fetch Permission from HQ.'); 
          this.isHq = true;       
        })

        return this.onError; 
    }

  /**
   * @name recentUsed
   * @description this function is arranging the recent services on the basis of latest click and storing it to cookie
   * @param obj 
   * 
   */
  private recentUsed(obj){
    let selectedServiceIndex = this.getRecent.indexOf(obj.title);
		if(selectedServiceIndex === -1 || selectedServiceIndex === null){
      if(this.getRecent.length < 10){
        this.getRecent.push(obj.title)
				this.getRecent.reverse();
			} else {	
				this.getRecent.splice(9,1);
				this.getRecent.splice(0,0,obj.title);
			}
		} else {
			this.getRecent.splice(selectedServiceIndex,1);
			this.getRecent.splice(0,0,obj.title);
		}

    this.cookies.store(`${this.env}_service_name`,this.getRecent.join(','),'',this.centerService.getSubDomainName());
	}
}
